        p El guía o conductor de grupo tiene la función de verificar que los equipos estén en condiciones óptimas antes, durante y después del recorrido, siguiendo los protocolos establecidos por la empresa operadora y la normativa vigente. Estos equipos no solo incluyen elementos de protección personal, sino también herramientas logísticas y de comunicación que aseguran la eficiencia y la coordinación durante la operación turística. A continuación, se muestra la tabla 3, en dónde se incluyen los diferentes tipos de equipos que se emplean esta clase de recorridos:

